import React, { Component, useState, useEffect }  from 'react';
import './index.css';
import safe_icon from '../../images/safe_icon.png'
import { useSelector } from "react-redux"
import { useDispatch } from "react-redux";
import { bindActionCreators } from 'redux'
import { createSafes, editSafes } from '../../store/action';


const Modal = ({show,setshow,name,owner,type,desc,setname,setowner,settype,setdesc,editid}) => {
//   useEffect(() => {
//       effect
//   }, [input])

  const showHideClassName = show ? "modal display-block" : "modal display-none";
  
  const state = useSelector((state) => state.safes.safesData);

  const dispatch = useDispatch();

  const  createSafesaction = bindActionCreators(createSafes, dispatch);

  const createsafeData = (e)=>{
      let obj = {name:name,owner:owner,type:type,desc:desc,id:Date.now()}
      createSafesaction(obj);
      setshow(false)
      setname("")
      setowner("")
      settype("Personal")
      setdesc("")
  }
  const  editSafesaction = bindActionCreators(editSafes, dispatch);

  const editsafesData = ()=>{
    let obj = {name:name,owner:owner,type:type,desc:desc,id:editid}
    editSafesaction(obj);
    setshow(false)
  }

  return (
    <div className="modal display-block">
      <form className="modal-main">
          <p className='form-head'>Create Safe</p>
          <div className="safes-form-info">
              <img src={safe_icon} alt="" className="safes-form-logo" />
              <p className='safes-form-text'>A Safe is a logical unit to store the secrets. All the safes are created within Vault. You can control access only at the safe level. As a vault administrator you can manage safes but cannot view the content of the safe.</p>
          </div>
          <div className="input-wrap">
              <div className="input-group">
                  <label htmlFor="name">Safe Name</label>
                  <input type="text" className='input' id='name' value={name} onChange={(e)=>setname(e.target.value)}/>
              </div>
              <div className="input-group">
                  <label htmlFor="owner">Owner</label>
                  <input type="text" className='input' id='owner' value={owner} onChange={(e)=>setowner(e.target.value)}/>
              </div>
              <div className="select-group">
                  <label htmlFor="type">Type</label>
                  <select name="" id="type" className='select' value={type} onChange={(e)=>settype(e.target.value)}>
                      <option value="Personal">Personal</option>
                      <option value="Other">Other</option>
                  </select>
              </div>
              <div className="input-group">
                  <label htmlFor="description">Description</label>
                  <textarea name="" id="description" className='input' rows="2" value={desc} onChange={(e)=>setdesc(e.target.value)}></textarea>
              </div>
              <div className="input-help">Please add a minimum of 10 characters</div>
              <div className="form-bottom-group">
                  {/* <button className="cancel" type='button' >Cancel</button> */}
                  <button className="create" type='button' onClick={()=>createsafeData()}>+ Create</button>
                  <button className='update' type='button' onClick={()=>editsafesData()}>Update</button>
              </div>
          </div>
          
              {/* <div className="form-bottom-group"> */}
                  {/* <button className="cancel" >Cancel</button> */}
                  {/* <button type='button' onClick={(e)=>createsafeData(e)}>+ Create</button> */}
              {/* </div> */}
        <button type="button" onClick={()=>setshow(false)}>
          Close
        </button>
      </form>
    </div>
  );
};

export default Modal;