import React from 'react'
import './index.css'
import search from '../../images/search.png'
import add from '../../images/add.png'
import add_folder from '../../images/add_folder.png'
import secret from '../../images/secret.png'
import { useState } from 'react';
import Modal from '../../components/Modal'
import { useSelector } from 'react-redux'
import safe_icon from '../../images/safe_icon.png'
import delete_icon from '../../images/delete_icon.png'
import edit from '../../images/edit.png'
import { useDispatch } from "react-redux";
import { deleteSafes } from '../../store/action'
import { bindActionCreators } from 'redux'



export const Safes = () => {
    const [name, setname] = useState("")
    const [owner, setowner] = useState("")
    const [type, settype] = useState("")
    const [desc, setdesc] = useState("")

    const [show, setshow] = useState(false)
    const state = useSelector((state) => state.safes.safesData);

    const [editid, seteditid] = useState(0)

    const handleedit = (id) => {
        seteditid(id)
        const temp = state.filter((element) => {
            if (element.id === id) {
                setname(element.name)
                setowner(element.owner)
                settype(element.type)
                setdesc(element.desc)
            }
        })
        setshow(true)
    }
    const dispatch = useDispatch();
    const  deleteSafesaction = bindActionCreators(deleteSafes, dispatch);
    const handledelete = (id) => {
        const larr = [...state]
        let temp = larr.filter(element=>element.id !==id)
        deleteSafesaction(temp)
    }

    return (
        <div className='container'>
            <div className="safes">
                <section className='safes-left'>
                    <div className='header-search-wrap'>
                        <p className='all-safes'>All Safes <span>(0)</span></p>
                        <div className="search-box">
                            <img src={search} alt="" className='search-icon' />
                            <input type="text" placeholder='Search' className='search-input' />
                        </div>
                    </div>
                    <div className='h1'></div>
                    {state.length == 0 ? <div className="left-bottom">
                        <div className="left-background"></div>
                        <div className="left-caption">Create a Safe and get started!</div>
                        <img src={add} alt="" className="left-add-img" onClick={() => setshow(true)} />
                    </div> : <div className='safes-cards-wrap'>{state.map((element) => { return <div className='safes-cards'><div className='safes-name-wrap'><img src={safe_icon} alt="" className='cards-safe-icon'/><div><div className='safes-name'>{element.name}</div><div className='update-text'>Last Updated: a few seconds ago</div></div></div><div className='edit-delete-wrap'><img src={edit} alt="" className='edit' onClick={() => (handleedit(element.id))}/><img src={delete_icon} alt="" className='delete-icon' onClick={()=>(handledelete(element.id))}/></div></div>})}
                    <img src={add} alt="" className="left-add-img" onClick={() => setshow(true)}/></div>}
                    <div className="model model-hidden"></div>
                </section>
                <section className='safes-right'>
                    <div className="right-banner">
                        <div className="right-captions">
                            <h3 className='no-safes-created'>No Safes Created Yet</h3>
                            <p className='right-description'>Create a Safe to see your secrets, folders and permissions here</p>
                        </div>
                    </div>
                    <div className="secrets">
                        <div className="secrets-header">
                            <p>Secrets</p>
                            <img src={add_folder} alt="" className="secret-add-folder" />
                        </div>
                        <div className="h1-1"></div>
                        <div className="secrets-count">0 Secrets</div>
                        <div className="secrets_list">
                            <img src={secret} alt="" className="secrets_img" />
                            <div className="secrets-caption">Add a <span className="secrets__caption-highlight">
                                Folder</span> and then you’ll be able to</div>
                            <div className="secrets-caption">add <span className="secrets__caption-highlight">Secrets </span>
                                to view them all here</div>
                            <button className="button">+ Add</button>
                        </div>
                    </div>
                </section>
            </div>
            {show && <Modal show={show} setshow={(bool) => setshow(bool)} name={name} owner={owner} type={type} desc={desc} setname={setname} setowner={setowner} settype={settype} setdesc={setdesc} editid={editid} />}
        </div>
    )
}
